
<template>
  <div class="container">
    <form>
      <div class="form-group">
        <input type="text" class="form-control" id="create-post" placeholder="Create post" v-model="text">
      </div>
      <button v-on:click="createPost" class="btn btn-primary">Submit</button>
    </form>
    <br/>
    <h2>Lates post</h2>
    <div class="alert alert-success"
      v-for="(post, index) in posts"
      v-bind:item="post"
      v-bind:index="index"
      v-bind:key="post._id"
      
    >
    <label class="date">{{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getDate()}` }} / <span v-on:click="deletePost(post._id)">Delete</span></label>
    
    <p>{{ post.text}}</p>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService';
export default {
  name: 'PostComponents',
  data() {
    return {
      posts: [],
      error: '',
      text: ''
      }
  },
  async created() {
    try{
      this.posts = await PostService.getPosts();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPost() {
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    }
  }
}

</script>

